<template>
  <div>
    <p>{{ $i18n.locale }}</p>
    <p>{{ $t("message") }}</p>
    <el-button type="primary" @click="changeLang">
      <span>{{ $t("lang") }}</span>
    </el-button>
  </div>
</template>

<script>
export default {
  name: "HelloI18n",
  methods: {
    changeLang() {
      const lang = this.$i18n.locale;
      if (lang === "zh") {
        this.$i18n.locale = "en";
      } else {
        this.$i18n.locale = "zh";
      }
    },
  },
};
</script>

<i18n>
{
  "en": {
    "hello": "Hello i18n in SFC!"
  },
  "zh": {
    "hello": "你好 i18n in SFC!"
  }
}
</i18n>
